.heroTxt {
    background-color: black;
    height: 22rem;
    width: 34rem;
    // padding-top: 3vh;
}

.whiteTxt {
    color: white;
    padding-top: 20vh;
}

hr.heroHr {
    width: 10%;
    border: 1px solid blue;
}

.herobanner {
    width: 55vw;
    height: 21rem;
    margin: 0.5rem 2.2rem 0rem 0rem;
}

.productImage {
    height: 200px;
    width: 50%;
    border: 1px solid gray;
    padding: 5px;
}

.Filter {
    text-align: left;
    padding-left: 5rem;
}

.card {
    cursor: pointer;
}

.prodTitle {
    font-size: 16px;
   // text-align: left;
    margin-left: 1rem;
    margin: 0%;
}
.resultcount{
    margin:2% 4% 2% 4%;
}
.price{
    margin:0%;
    text-align: left;
    margin-left: 5rem;
}



@media only screen and (min-width:320px) and ( max-width:768px) {
    .heroBanner{
        order:1;
    }
    .heroTxt{
        order:2;
    }
    .productImage{
        width:35vw;
    }
   .sortBy{
    display:none;
   }
   .resultcount{
    text-align: center !important;
   }

   .heroImg{
    display: flex;
    flex-direction: column;
   }
   .herobanner{
    width: 100vw;
    height: 12rem;
    margin: -3%;
    padding: 0%;

   }
   .heroTxt{
    height:10rem;
    width:21rem;
     margin-top: -10%;
   }
   .whiteTxt{
    padding-top: 7vh;
   }
}


@media only screen and (min-width:768px) {
.phoneFilters{
    display: none;
}

}