@media screen and (min-width:320px) and (max-width:767px) {
    .editremove{
        display:none;
    }
    .imageprops{
        height: 20vh;
        width: 35vw;
    }   
    .Cart{
        margin:0%;
    }
    .quanBar{
        margin: 0% 0% 0% 40%;
    }
    .discount{
        margin:4%;
         padding: 2%;
    }
    .border{
        padding:1rem 1rem 2rem 1rem;
        width: 100vw;
    }
    .discrightalign{
        float: left;
    }
    .rightalign{
        float: right;
    }
    .pricing{
        text-align: left;
    }
    .totalSummary{
       padding:2rem;
    }
    .checkoutBtn{
        @include button;
        background-color: #1F39FF;
    }
    .quantity{
        width: 4rem;
        border-radius: 5px;
    }
    .paypal{
        margin-left: 8vw;
    }
}


@media screen and (min-width:768px) and (max-width:1023px) {
   .imageprops{
    height:10vh;
   }
   .border{
    border: 1px solid gray;
    margin: 0%;
    padding: 1rem;
}
.discount{
    margin:0%;
}
.rightalign{
    float: right;
}
.pricing{
    text-align: left;
}
.totalSummary{
   padding:2rem;
}
.checkoutBtn{
    @include button;
    background-color: #1F39FF;
    margin-left: 30vw;
}
.paypal{
    margin-left: 30vw;
}
.quantity{
    width: 4rem;
    border-radius: 5px;
}
.Cart{
    margin:1rem;
}
}