.clrdiv{
    height:3vh;
    width:6vw;
}
.sortBy{
    float:right;
    height:1.2rem;
    border-radius: 6px;
    width:12rem;
}



.resultcount{
   text-align: left;
}

.checkbox {
    margin:0% ;
}
.Filter{
    text-align: left;
    padding-left: 1rem;
}
ul.pagination{
    list-style: none;
    display: inline-flex;
     a{
      color:black;
     }
    li{
      padding:0.5rem;
      text-decoration: none;     
        :focus{
          color:blue;
        }
    }
}

@keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  .loading-spinner {
    width: 50px;
    height: 50px;
    border: 10px solid #f3f3f3; /* Light grey */
    border-top: 10px solid #383636; /* Black */
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
  }


  @media only screen and ( min-width:768px) and (max-width:1023px) {

    .colorFilterImg{
        width:10rem;
    }
    }